import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { routerNgProbeToken } from '@angular/router/src/router_module';
import { FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { Recipe } from '../recipe.model';
import { RecipesService } from '../recipes.service';

@Component({
  selector: 'app-recipe-edit',
  templateUrl: './recipe-edit.component.html',
  styleUrls: ['./recipe-edit.component.css']
})
export class RecipeEditComponent implements OnInit {
 id:number;
 isEdit:boolean;
 recipeForm: FormGroup;
 editedRecipe : Recipe;
  constructor(private route:ActivatedRoute,private recipeService: RecipesService,private router:Router) { }

  ngOnInit() {
    this.route.params.subscribe(
      (params:Params) => 
      {
        this.id = +params['id'];
        this.isEdit = params['id'] != null;
       if(this.isEdit)
       {
       this.editedRecipe = this.recipeService.GetRecipe(this.id);
      }
      this.InitForm();
     }
    )
    ;
  }

  getControls() {
    return (<FormArray>this.recipeForm.get('ingredients')).controls;
  }
  
  InitForm()
  {
    let name = '';
    let description = '';
    let imagePath = ''; 
    let formArr = new FormArray([]);

    if(this.isEdit)
    {
      name = this.editedRecipe.name;
      description = this.editedRecipe.description;
      imagePath = this.editedRecipe.imagePath;
      if(this.editedRecipe.ingredients.length > 0)
      {
        for(let ingredient of this.editedRecipe.ingredients)
        {
          formArr.push(new FormGroup({
            'name' : new FormControl(ingredient.name,[Validators.required,Validators.pattern(/^[a-zA-Z]+$/)]),
            'amount': new FormControl(ingredient.amount,[Validators.required,Validators.pattern(/^[1-9]+[0-9]*$/)])
          }))
        }
      }
    }
    this.recipeForm = new FormGroup({
      'name': new FormControl(name,Validators.required),
      'description': new FormControl(description,Validators.required),
      'imagePath': new FormControl(imagePath,Validators.required),
      'ingredients':formArr
    })
  }

  OnRemoveIngredient(index:number)
  {
    (<FormArray>this.recipeForm.get('ingredients')).removeAt(index);
  }
  onAddIngridient()
  {
    (<FormArray>this.recipeForm.get('ingredients')).push(new FormGroup({
      'name' : new FormControl(null,[Validators.required,Validators.pattern(/^[a-zA-Z]+$/)]),
      'amount': new FormControl(null,[Validators.required,Validators.pattern(/^[1-9]+[0-9]*$/)])
    }));
  }

  OnSubmit()
  {
    
      const name = this.recipeForm.get('name').value;
      const description = this.recipeForm.get('description').value;
      const imagePath = this.recipeForm.get('imagePath').value;
      const ingredients = this.recipeForm.get('ingredients').value;
       let recepie = new Recipe(name,description,imagePath,ingredients);

       if(this.isEdit)
       {
         this.recipeService.UpdateRecipe(this.id,recepie);
       }
       else
       {
         this.recipeService.AddRecipe(recepie);
       }
       this.NavigateBack();
  }

  OnCanceled()
  {
    this.NavigateBack();
  }
  NavigateBack()
  {
    this.router.navigate(['..'],{relativeTo:this.route});
  }
}
