import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Recipe } from '../recipe.model';
import { RecipesService } from '../recipes.service';
import { Subscription } from 'rxjs';
import { DataAcessService } from 'src/app/Shared/data-acess.service';

@Component({
  selector: 'app-recipe-list',
  templateUrl: './recipe-list.component.html',
  styleUrls: ['./recipe-list.component.css']
})
export class RecipeListComponent implements OnInit,OnDestroy {
 recepieSubscription: Subscription;
recepieSearched : string;

  ngOnDestroy(): void {
    this.recepieSubscription.unsubscribe();
  }
 
  recipes : Recipe[] = [];

 
  constructor(private recipeService:RecipesService,private dataAcess:DataAcessService) 
  { 
      
  }
  
  
  ngOnInit() {
    this.recipes = this.recipeService.GetRecipes();
    this.recepieSubscription = this.recipeService.recepieChanged.subscribe(
      (recepies:Recipe[]) => {
        this.recipes = recepies;
      }
    )
   
  }


}
